.hizmet {
	transition: all 0.3s ease-out 0s;
	-webkit-transition: all 0.3s ease-out 0s;
}

@media screen and (max-width: 1230px) {
	.service-con {
		@apply flex-col
	}
	img {
		@apply mx-auto
	}
	.service-text {
		@apply pl-0
	}
}


@media screen and (max-width: 768px) {
	.service-ul {
		@apply flex-col gap-6
	}
}


@media screen and (max-width: 600px) {
	.hizmet {
		@apply text-[40px]
	}
}