.sidebar {
	background: #ffffff;
  height: 100%;
  position: fixed;
  z-index: 9999;
  right: -200%;
  top: 0;
  width: 465px;
  padding: 45px;
  transition: 0.6s;
  overflow-y: auto;
	box-shadow: -5px 1px 20px 4px rgba(23, 45, 61, 0.2)
}
.header {
	transition: all 0.3s ease-out 0s;
	-webkit-transition: all 0.3s ease-out 0s;
}
.sidebar.active {
	right: 0;
}


.drawer {
	z-index: 0;
}

@media screen and (max-width: 1230px) {
	.nav-menu {
		@apply hidden
	}

	.sidebar-menu {
		@apply block
	}

	.sidebar-text {
		@apply hidden
	}
}

@media screen and (max-width: 620px) {
	.header-con {
		@apply w-full mt-[-20px]
	}

	.menu {
		@apply w-[70px]
	}

	.menu i {
		@apply text-[40px]
	}

	.sidebar {
		@apply w-[400px]
	}
}


@media screen and (max-width: 450px) {
	.sidebar {
		@apply w-[350px]
	}
}