@tailwind base;
@tailwind components;
@tailwind utilities;


body {
	overflow-x: hidden;
	transition: all 0.3s ease-out 0s;
	-webkit-transition: all 0.3s ease-out 0s;
}

h1, h2, h3, h4, p, a, button, span, img {
	transition: all 0.3s ease-out 0s;
	-webkit-transition: all 0.3s ease-out 0s;
}

.container {
	@apply w-[80%] mx-auto;

	transition: all 0.3s ease-out 0s;
	-webkit-transition: all 0.3s ease-out 0s;
}

img {
	max-width: 100%;
}

@media screen and (max-width: 1200px) {
	.container {
		@apply w-[95%] mx-auto
	}
}