.blog-p::before {
	content: "";
	position: absolute;
	background: #aed137;
	width: 1px;
	height: 50px;
	left: -20px;
}

@media screen and (max-width: 1000px) {
	.blog-con {
		@apply mb-[100px]
	}

	.blog-head {
		@apply flex-col items-start
	}
}

@media screen and (max-width: 900px) {
	.blog-box {
		@apply flex-col
	}

	.blog-con {
		@apply mb-[80px]
	}
}


@media screen and (max-width: 768px) {
	.blog-head h1 {
		@apply text-[35px]
	}
}