.on-eki {
	-webkit-text-stroke-color: #3682f6;
	-webkit-text-fill-color: transparent;
  font-size: 120px;
  color: #3682f6;
  -webkit-text-stroke-color: #3682f6;
  -webkit-text-stroke-width: 1px;
  -moz-text-stroke-color: #3682f6;
  -moz-text-stroke-width: 1px;
  -moz-text-fill-color: transparent;
  line-height: 90px;
  display: inline-block;
	font-weight: 700;
}


.slick-prev {
	@apply bg-[#3682f6] p-4
}


@media screen and (max-width: 1000px) {
	.tran {
		@apply flex-col
	}

	.slide-con {
		@apply w-[80%] mx-auto
	}

	.slide {
		@apply w-full h-[350px] mt-8
	}

	.slide img {
		@apply w-full h-[350px]
	}

	.tran-left {
		@apply px-[5%]
	}

	.on-eki {
		@apply text-[80px]
	}
	.tran-h1 {
		@apply text-[50px]
	}
}


@media screen and (max-width: 600px) {
	.slide {
		@apply h-[250px]
	}

	.slide img {
		@apply h-[250px]
	}
}