.contact-box:hover i {
	transform: translateY(-10px);
}
.contact-box i {
	transition: all 0.3s ease-out;
}



@media screen and (max-width: 1000px) {
	.contact-con {
		@apply flex-col
	}

	.contact-left {
		@apply w-full
	}

	.contact-right {
		@apply w-full
	}
}


@media screen and (max-width: 600px) {
	.con-h1 {
		@apply text-[35px]
	}

	.phone {
		@apply text-[25px]
	}
}