.fot-text::before {
	position: absolute;
	content: "";
	bottom: -15px;
	left: 0;
	width: 100px;
	height: 2px;
	background: #333333;
}

.fot-text::after {
	position: absolute;
	content: "";
	width: 30px;
	height: 2px;
	bottom: -15px;
	left: 0;
	background: #3682f6;
	-webkit-transition: all 0.3s ease-out 0s;
	-moz-transition: all 0.3s ease-out 0s;
	-ms-transition: all 0.3s ease-out 0s;
	-o-transition: all 0.3s ease-out 0s;
	transition: all 0.3s ease-out 0s;
}

.fot-con:hover .fot-text::after {
	width: 100px;
}

@media screen and (max-width: 768px) {
	.footer {
		@apply pb-[30px]
	}
	.fot-con-st {
		@apply w-full
	}

	.fot-con {
		@apply mb-[50px]
	}
}


@media screen and (max-width: 600px) {
	.fot-our {
		@apply flex-col
	}
}